<template>
  <div>
    <div style="margin: 10px 0">
      <el-input style="width: 200px" placeholder="请输入用户名" suffix-icon="el-icon-search" v-model="username"></el-input>
      <el-input style="width: 200px" placeholder="请输入电子邮箱" class="ml-5" suffix-icon="el-icon-message" v-model="email"></el-input>
      <el-input style="width: 200px" placeholder="请输入电话号码" class="ml-5" suffix-icon="el-icon-message" v-model="phone"></el-input>
      <!--      <el-input style="width: 200px" placeholder="请输入出版社" class="ml-5" suffix-icon="el-icon-message" v-model="publisher"></el-input>-->
      <!--      <el-input style="width: 200px" placeholder="请输入书籍名称" class="ml-5" suffix-icon="el-icon-message" v-model="tnumber"></el-input>-->
      <!--      <el-input style="width: 200px" placeholder="请输入地址" class="ml-5" suffix-icon="el-icon-position" v-model="lcredit"></el-input>-->
      <!--      <el-input style="width: 200px" placeholder="请输入开课学院号" class="ml-5" suffix-icon="el-icon-position" v-model="lcollege"></el-input>-->
      <el-button class="ml-5" type="primary" @click="load">搜索</el-button>
      <el-button type="warning" @click="reset">重置</el-button>
    </div>

    <div style="margin: 10px 0">


      <!--      <el-button type="primary" class="ml-5" @click="EBookup">导入<i class = "el-icon-bottom"></i></el-button>-->
      <el-button type="primary" class="ml-5" @click="handleEdit2">新增用户<i class = "el-icon-bottom"></i></el-button>
      <el-button type="primary" @click="exp" class="ml-5">导出<i class = "el-icon-top"></i></el-button>
    </div>

    <el-table :data="tableData" border stripe header-cell-class-name="headerBg" @selection-change="handleSelectionChange">
      <!--          多选框-->
      <!--      <el-table-column type="selection" width="55" />-->
      <el-table-column prop="username" label="用户名" width="120">
      </el-table-column>
      <el-table-column prop="password" label="密码" width="150">
      </el-table-column>
      <el-table-column prop="email" label="电子邮箱">
      </el-table-column>
      <el-table-column prop="phone" label="电话号码">
      </el-table-column>
      <!--      <el-table-column prop="category" label="类别">-->
      <!--      </el-table-column>-->
      <!--      <el-table-column prop="releasetime" label="上传时间">-->
      <!--      </el-table-column>-->
      <el-table-column label="操作" width="200">
        <template slot-scope="scope">
          <el-button type="success" @click="handleEdit(scope.row)">编辑<i class="el-icon-edit"></i></el-button>
          <el-popconfirm
              class="ml-5"
              confirm-button-text="确认"
              cancel-button-text="取消"
              :icon="InfoFilled"
              icon-color="#626AEF"
              title="是否删除?"
              @confirm="del(scope.row.id)"
              @cancel="cancelEvent"
          >
            <template #reference>
              <el-button type="danger">删除<i class="el-icon-remove-outline"></i></el-button>
            </template>
          </el-popconfirm>

        </template>
      </el-table-column>

    </el-table>
    <div style="padding: 10px 0" >
      <el-pagination
          v-model:current-page="currentPage4"
          v-model:page-size="pageSize"
          :current-page="pageNum"
          :page-sizes="[2, 5, 10, 20]"
          :page-size="pageSize"
          :small="small"
          :disabled="disabled"
          :background="background"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />

    </div>
    <!--    编辑  弹出的界面-->
    <el-dialog title="书籍信息" :visible.sync="dialogFormVisible" width="30%" >
      <el-form label-width="80px" size="small">
        <el-form-item label="用户名">
          <el-input v-model="form.username" autocomplete="off" />
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="form.password" autocomplete="off" />
        </el-form-item>
        <el-form-item label="电子邮箱">
          <el-input v-model="form.email" autocomplete="off" />
        </el-form-item>
        <el-form-item label="电话号码">
          <el-input v-model="form.phone" autocomplete="off" />
        </el-form-item>
        <!--        <el-form-item label="类别">-->
        <!--          <el-input v-model="form.category" autocomplete="off" />-->
        <!--        </el-form-item>-->
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取消</el-button>
        <el-button type="primary" @click="save">
          确认
        </el-button>
      </span>
      </template>
    </el-dialog>

    <!--    新增用户  弹出的界面-->
    <el-dialog title="书籍信息" :visible.sync="UserFormVisible" width="30%" >
      <el-form label-width="80px" size="small">
        <el-form-item label="用户名">
          <el-input v-model="form.username" autocomplete="off" />
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="form.password" autocomplete="off" />
        </el-form-item>
        <el-form-item label="电子邮箱">
          <el-input v-model="form.email" autocomplete="off" />
        </el-form-item>
        <el-form-item label="电话号码">
          <el-input v-model="form.phone" autocomplete="off" />
        </el-form-item>
        <!--        <el-form-item label="类别">-->
        <!--          <el-input v-model="form.category" autocomplete="off" />-->
        <!--        </el-form-item>-->
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="UserFormVisible = false">取消</el-button>
        <el-button type="primary" @click="save">
          确认
        </el-button>
      </span>
      </template>
    </el-dialog>
  </div>

</template>

<script>
import EBookUpload from "@/views/Admins/EBookUpload.vue";

export default {
  name: "User",
  computed: {
    EBookUpload() {
      return EBookUpload
    }
  },
  data(){
    return{
      tableData: [],
      total: 0,
      pageNum: 1,
      pageSize: 10,

      id: "",
      username: "",
      email: "",
      phone: "",
      password: "",
      currentsize: "",
      dialogFormVisible: false,
      UserFormVisible: false,
      multipleSelection: [],
      form: {},
      headerBg: 'headerBg'

    }
  },
  created() {
    this.load()
  },
  methods:{
    load(){
      //请求分页查询数据
      //通过axios向后台请求参数
      //通过request.js中的baseurl已经将前面的http://localhost:9090部分省略了
      this.request.get("/users/page",{
        params:{
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          username: this.username,
          email: this.email,
          phone: this.phone,

        }
      })
          .then(res => {
            console.log(res)
            this.tableData = res.records
            this.total = res.total
          })

    },

    save(){
      //发送数据到后端
      this.request.post("/users/save",this.form)
          .then(res => {
            if(res){
              this.$message.success("保存成功")
              this.dialogFormVisible = false
              this.UserFormVisible = false
              this.load()
            }else {
              this.$message.error("保存失败")
            }
          })
    },
    handleEdit(row){
      this.form = row //将数据赋予弹窗
      this.dialogFormVisible = true //显示弹窗

    },
    handleEdit2(row){
      //this.form = row //将数据赋予弹窗
      this.UserFormVisible = true //显示弹窗

    },
    handleAdd(){
      this.dialogFormVisible = true
      this.form = {}
    },
    // handleEdit(row){
    //   this.form = row //将数据赋予弹窗
    //   this.dialogFormVisible = true //显示弹窗
    //
    // },
    // del(id){
    //   this.request.delete("/lesson/" + id)
    //       .then(res => {
    //         if(res){
    //           this.$message.success("删除成功")
    //           this.load()
    //         }else {
    //           this.$message.error("删除失败")
    //         }
    //       })
    // },
    handleSelectionChange(val){
      console.log(val)
      this.multipleSelection = val
    },
    EBookup(){
      this.$router.push("/admins/ebookupload")
    },
    // delBatch(){
    //   let ids = this.multipleSelection.map(v => v.lnumber)   //把一个对象的数组变成一个纯数组
    //   this.request.post("/department/del/batch",ids).then(res => {
    //     if(res){
    //       this.$message.success("批量删除成功")
    //       this.load()
    //     }else {
    //       this.$message.error("批量删除失败")
    //     }
    //   })
    // },
    reset(){
      this.email = ""
      this.username = ""
      this.phone = ""
      this.load()
    },
    handleSizeChange(pageSize){
      this.pageSize = pageSize
      this.load()
    },
    handleCurrentChange(pageNum){
      this.pageNum = pageNum
      this.load()
    },
    exp(){
      window.open("http://localhost:9090/users/export")
    },
    del(id){
      this.request.delete("/users/" + id).then(res => {
        if(res.code === '200'){
          this.$message.success("删除成功")
          this.load()
        }else {
          this.$message.error("删除失败")
        }
      })
    },
    handleExcelImportSuccess(){
      this.$message.success("文件上传成功！")
      this.load()
    }
  }
}
</script>

<style>
.headerBg{
  background: #ccc !important;
}
</style>